.form-area {
    height: 90vh;
    padding: 4rem;
}

.formarea-wrapper {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    max-width: 1500px;
    width: 90%;
    height: 100%;
    margin: 0 auto;
}

.contact-form {
    width: 60%;
    height: 80%;
    border-radius: 10px;
    padding: 2rem;
    background-color: #242424;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center ;
}

form > h4 {
    font-size: 3rem;
    text-align: center;
    margin-bottom: 2rem;
}


input[type=text],
input[type=number],
textarea {
    color: #fff;
    font-size: 1rem;
    width: 100%;
    padding: 12px;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid #fff;
    box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 16px;
    resize: vertical
}


input[type=submit] {
    margin: 0 auto;
    background-color: var(--primary-color);
    color: #242424;
    font-size: 1rem;
    border: 1px solid var(--primary-color);
    padding: 20px 120px;
    border-radius: 10px;
    outline: none;
    cursor: pointer;
    transition: all 0.3s ease;
}

input[type=text]:focus,
input[type=number]:focus,
textarea:focus {
    background-color: transparent;
    outline: none;
    color: #fff;
}

input[type=text]::placeholder,
input[type=number]::placeholder,
textarea::placeholder {
    font-size: 1rem;
    color: #fff;
    background-color: transparent;
}


input[type=submit]:hover {
    background-color: transparent;
    color: white;
    border: 1px solid white;
}


.container {
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
}


@media screen and (max-width: 1500px) {
    .contact-form {
        min-width: 80%;
        height: auto;
    }
}

@media screen and (max-width: 890px) {
    .form-area {
        padding: 1rem;
    }
    .contact-form {
        min-width: 90%;
        height: auto;
    }

    .contact-form > h4 {
        font-size: 1.5rem;
    }

    input[type=submit] {
        
        padding: 15px 80px;
        
    }
}

@media screen and (max-width: 500px) {
    .contact-form {
        min-width: 100%;
        height: auto;
    }
} 