.aboutme {
    padding: 4rem;
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    color: #fff;
}

.aboutme-wrapper {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1500px;
    width: 90%;
    margin: 0 auto;
}

.aboutme-introduction-area {
    height: auto;
    text-align: center;
    margin-bottom: 4rem;
}

.aboutme-introduction-area > h4 {
    font-size: 3rem;
    font-weight: bold;
    margin: 2rem 0;
}

.aboutme-introduction-area > p {
    font-size: 1.5rem;
    font-weight: 500;
}

.aboutme-main-area {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    min-height: 600px;
    text-align: center;
}

.image-area {
    width: 45%;
    height: 100%;
}

.image-area img {
    border-radius: 20px;
    width: 100%;
    max-height: 80%;
    object-fit: cover;
}

.description-area {
    padding: 0 3rem;
    width: 50%;
    height: 100%;
    font-size: 1.5rem;
}


@media screen and (max-width: 1756px) {
    .aboutme {
        height: auto;
        padding: 2rem;
    }
    .aboutme-wrapper {
        font-size: 1.4rem;;
    }
    .aboutme-introduction-area {
        margin-bottom: 4rem;
    }

    .aboutme-main-area {
        flex-direction: column;
    }

    .description-area {
        width: 100%;
        font-size: 1.2rem;
        margin-bottom: 1rem;
    }

    .image-area {
        margin: 1rem auto 2rem auto;
        width:50%;
    }
}

@media screen and (max-width: 1756px){
    .aboutme-introduction-area {
        margin-bottom: 1rem;
    }
    .image-area {
        min-width: 60%;
    }
    .aboutme-main-area {
        margin-bottom: 1rem;
    }
}

@media screen and (max-width: 1200px){
    .aboutme-introduction-area > h4 {
        font-size: 1.5rem;
        font-weight: bold;
        margin: 2rem 0;
    }
    
    .aboutme-introduction-area > p {
        font-size: 1.4rem;
        font-weight: 500;
    }
    .image-area {
        min-width: 70%;
    }
    
}

@media screen and (max-width: 700px){
    .aboutme {
        text-align: center;
    }
    .image-area {
        min-width: 90%;
        min-height: 100%;
    }
    
}